@use "../../../assets/styles/variables" as *;
@use "../../../assets/styles/mixins" as *;

.page-transition {
  position: relative;
  
  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: $main-color;
    transform: scaleY(0);
    z-index: 9999;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    
    @include media($breakpoints-tablet) {
      display: none;
    }
  }
  
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    position: relative;
    z-index: 10;
  }
  
  &__text {
    color: $white-color;
    font-size: 2.5rem;
    font-weight: 700;
    opacity: 0;
    transform: translateY(30px);
    text-align: center;
    
    @include media($breakpoints-mobile) {
      font-size: 1.5rem;
    }
  }
  
  &__page-name {
    color: $accent-color;
    font-size: 1.8rem;
    font-weight: 600;
    opacity: 0;
    transform: translateX(50px);
    text-align: center;
    background: rgba($white-color, 0.1);
    padding: 8px 20px;
    border-radius: 30px;
    box-shadow: 0 0 15px rgba($accent-color, 0.3);
    
    @include media($breakpoints-mobile) {
      font-size: 1.2rem;
    }
  }
  
  &__flying-section {
    position: absolute;
    top: -100px;
    left: 0;
    width: 100%;
    height: 100px;
    background: linear-gradient(90deg, $accent-color, $main-color);
    transform: skewY(-5deg);
    opacity: 0;
  }
}
